import "../src/css/App.css";
import ReactFullpage from "@fullpage/react-fullpage";
import { Button } from "react-bootstrap";
import { useEffect } from "react";

const anchors = ["PrimeraPagina", "SegundaPagina", "thirdPage"];

function App() {
  useEffect(() => {
    fetch("http://localhost:4000/api/productos/", {
      method: "GET",
      mode: "cors",
      headers: {
        Accept: "application/json",
        "Content-type": "application/json",
      },
    })
      .then((res) => res.json())
      .then(
        (res) => {
          console.log(res);
        },

        (error) => {
          console.log(error);
        }
      );
  }, []);
  return (
    <div className="App">
      <ReactFullpage
        licenseKey={"YOUR_KEY_HERE"}
        scrollingSpeed={1500} /* Options here */
        anchors={anchors}
        navigation
        navigationTooltips={anchors}
        sectionsColor={[]}
        onLeave={(origin, destination, direction) => {
          if (origin.anchor === "PrimeraPagina") {
            let aux = document.querySelector("#item-left-h1");
            let aux1 = document.querySelector("#item-left-h4");
            let aux2 = document.querySelector("#item-left-h4-2");
            aux.classList.replace("text-focus-in", "text-focus-offLeft");
            aux1.classList.replace("text-focus-in-2", "text-focus-offLeft-2");
            /* aux2.classList.replace("text-focus-end", "text-focus-offRigth"); */
            aux2.classList.replace("text-focus-in-3", "text-focus-offLeft-3");
          }
          if (destination.anchor === "PrimeraPagina") {
            let aux = document.querySelector("#item-left-h1");
            let aux1 = document.querySelector("#item-left-h4");
            let aux2 = document.querySelector("#item-left-h4-2");
            aux.classList.replace("text-focus-offLeft", "text-focus-in");
            aux1.classList.replace("text-focus-offLeft-2", "text-focus-in-2");
            aux2.classList.replace("text-focus-offLeft-3", "text-focus-in-3");
            /* aux2.classList.replace("text-focus-offRigth", "text-focus-end"); */
          }
          if (origin.anchor === "SegundaPagina") {
            let aux = document.querySelector("#item-left-h1-s2");
            aux.classList.replace("text-focus-in", "text-focus-offLeft");
          }
          if (destination.anchor === "SegundaPagina") {
            let aux = document.querySelector("#item-left-h1-s2");
            aux.classList.replace("text-focus-offLeft", "text-focus-in");
          }
        }}
        render={({ state, fullpageApi }) => {
          return (
            <ReactFullpage.Wrapper>
              <div className="section">
                <section
                  style={{
                    height: "100%",
                    display: "flex",
                    alignItems: "center",
                  }}
                  className="row img"
                >
                  <div className="col-md-6">
                    <h1 className="text-s1 text-focus-in" id="item-left-h1">
                      CARLOS <br />
                      ROA
                    </h1>
                    <h4
                      className="text-s1-sub text-focus-in-2"
                      id="item-left-h4"
                    >
                      FULL STACK DEVELOPER JAVASCRIPT
                    </h4>
                    <hr />
                    <h4
                      className="text-s1-2 text-focus-in-3"
                      id="item-left-h4-2"
                    >
                      MI PORTAFOLIO
                    </h4>
                  </div>
                  <div
                    className="col-md-6 text-focus-end row justify-content-center"
                    id="item-rigth"
                  ></div>

                  {/* <button onClick={() => fullpageApi.moveSectionDown()}>
                    Click me to move down
                  </button> */}
                </section>
              </div>
              <div className="section">
                <section
                  style={{
                    height: "100%",
                    display: "flex",
                    alignItems: "center",
                    backgroundColor: "#0f1d40",
                  }}
                  className="row"
                >
                  <div className="col-md-4">
                    <h1
                      style={{
                        color: "#fff",
                        paddingLeft: "10%",
                        fontSize: "60px",
                        fontWeight: 800,
                      }}
                      className="text-focus-in"
                      id="item-left-h1-s2"
                    >
                      Pachamamapp
                    </h1>
                    <div style={{ marginTop: "60px" }}>
                      <Button variant="primary">Primary</Button>
                      <Button variant="primary">Primary</Button>
                    </div>
                  </div>
                  <div className="col-md-8">
                    <div className="image-border"></div>
                  </div>
                </section>
              </div>
              <div className="section"></div>
            </ReactFullpage.Wrapper>
          );
        }}
      />
    </div>
  );
}

export default App;
